@import url("https://fonts.googleapis.com/css2?family=Meddon&display=swap");

body {
  background: url("../images/biriyani.jpg") fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

// NAVBAR

nav {
  text-align: center;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    from(rgba(3, 38, 35, 0.3)),
    to(rgba(3, 38, 35, 0.7))
  );
  background: linear-gradient(
    to bottom right,
    rgba(3, 38, 35, 0.3),
    rgba(3, 38, 35, 0.7)
  );
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;

  .logo {
    color: yellow;
    font-family: "Meddon", cursive;
    font-size: 1.8rem;
    margin-top: -0.3em;
  }

  .nav-links {
    list-style-type: none;
    padding-left: 0;
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }

  .nav-link {
    padding: 0.2em 0.5em;
    font-size: 1.4rem;
    cursor: pointer;
    text-transform: uppercase;
  }
}

img {
  width: 20%;
}
